:root {
  --color-sidebar: rgb(86, 118, 179);
  --color-textSidebar: white;
}

.Pagination {
  display: flex;
  position: absolute;
  transform: translateX(90%);
  list-style: none;
}

.pageItem {
  text-align: center;

  border-radius: 10px;
  padding: 0;
  margin: 1rem;
}

.pageItem.on {
  background-color: white;
}

.Pagination button {
  height: 60px;
  width: 70px;
  color: white;
  border: 3px solid var(--color-sidebar);
  background-color: var(--color-sidebar);
  border-radius: 10px;
  font-size: 2rem;
  margin-top: 30px;
  margin-bottom: 70px;
  scale: 90%;
}

.Pagination button:hover {
  background-color: white;
  color: var(--color-sidebar);
  border: 3px solid var(--color-textSidebar);
  cursor: pointer;
}

.Pagination button.active {
  scale: 110%;
  background-color: white;
  color: var(--color-sidebar);
}

.endSearch {
  font-size: 5rem;
  padding: 50px;
  color: white;
  filter: drop-shadow(2px 2px 4px black);
  transform: translateX(-20px);
}

@media screen and (max-width: 1366px) {
  .endSearch {
    font-size: 2rem;
    padding: 10px;
  }
}

@media screen and (max-width: 450px) {
  .Pagination {
    display: none;
    padding: 0;
  }

  .endSearch {
    position: absolute;
    transform: translate(5%, 30%);
    padding: 1rem;
    max-width: 70vmin;
  }

  .endSearch h2 {
    font-size: 2rem;
  }
}

@media screen and (max-width: 360px) {
  .Pagination {
    display: none;
    padding: 0;
  }

  .endSearch {
    position: absolute;
    transform: translate(5%, 30%);
    padding: 1rem;
    max-width: 70vmin;
  }

  .endSearch h2 {
    font-size: 2rem;
  }
}
